import { model, Schema, models } from 'mongoose';
import ProductsModel from '../../products/models/products';
import ProductionLinesModel from '../../production line/models/production_line';

export default function production() {
  const collectionName = 'production';
  if (models[collectionName]) {
    return model(collectionName);
  }
  const productionSchema = new Schema({
    line: {
      type: Schema.Types.ObjectId,
      required: true,
      ref: ProductionLinesModel().modelName,
    },
    productionType: {
      type: String,
      required: true,
    },
    product: {
      type: Schema.Types.ObjectId,
      ref: ProductsModel().modelName,
      required: true,
    },
    batchNumber: {
      type: String,
      required: true,
    },
    batchQuantity: {
      type: Number,
    },
    packType: {
      type: String,
    },
    healStartCode: {
      type: String,
    },
    bottles: {
      type: Number,
    },
    cases: {
      type: Number,
    },
    status: {
      type: String,
    },
  });

  return model(collectionName, productionSchema);
}
